"use client";

import SpeechRecognition, { useSpeechRecognition } from "react-speech-recognition";
import { useEffect } from "react";

interface DictaphoneProps {
  appendToMessage: (text: string) => void; // new prop
}

const Dictaphone: React.FC<DictaphoneProps> = ({ appendToMessage }) => {
  const { transcript, listening, resetTranscript, browserSupportsSpeechRecognition } =
    useSpeechRecognition();

  // Whenever transcript changes, append it to the parent textarea
  useEffect(() => {
    if (transcript) {
      appendToMessage(transcript);
    }
  }, [transcript, appendToMessage]);

  if (!browserSupportsSpeechRecognition) {
    return <span>Browser doesn't support speech recognition.</span>;
  }

  return (
    <div className="flex flex-col items-center gap-3 w-full mt-2">
      <p className="text-sm text-gray-300">Microphone: {listening ? "on 🎙️" : "off 🔇"}</p>
      <div className="flex gap-2">
        <button
          onClick={() =>
            SpeechRecognition.startListening({ continuous: true, language: "en-US" })
          }
          className="px-4 py-2 bg-linear-to-r from-purple-500 to-pink-500 rounded-lg text-white font-semibold hover:opacity-90 transition-all"
        >
          Start
        </button>
        <button
          onClick={() => SpeechRecognition.stopListening()}
          className="px-4 py-2 bg-gray-700 rounded-lg hover:bg-gray-600 transition-all"
        >
          Stop
        </button>
        <button
          onClick={resetTranscript}
          className="px-4 py-2 bg-pink-600 rounded-lg hover:bg-pink-500 transition-all"
        >
          Reset
        </button>
      </div>
    </div>
  );
};

export default Dictaphone;
